import React, { useState } from 'react';

export const AddCommentForm = ({ articleName, setArticleInfo }) => {
  const [userName, setUserName] = useState('');
  const [comment, setComment] = useState('');

  function usernameEventHandler(event) {
    setUserName(event.target.value);
  }

  function commentEventHandler(event) {
    setComment(event.target.value);
  }

  const uploadComment = async () => {
    const result = await fetch(`/api/articles/${articleName}/add-comment`, {
      method: 'post',
      body: JSON.stringify({ username: userName, text: comment }),
      headers: {
        'Content-Type': 'application/json',
      },
    });
    const body = await result.json();
    setArticleInfo(body);
    setUserName('');
    setComment('');
  };

  return (
    <div id='add-comment-form'>
      <h3>Add a Comment</h3>
      <label>
        Name:{' '}
        <input
          type='text'
          value={userName}
          onChange={usernameEventHandler}
        ></input>
      </label>

      <label>
        Comment:{' '}
        <textarea
          rows='4'
          cols='50'
          value={comment}
          onChange={commentEventHandler}
        ></textarea>
      </label>

      <button onClick={() => uploadComment()}>Add Comment</button>
    </div>
  );
};

export default AddCommentForm;
